.App {
	text-align: center;
	height: 100vh;
	margin: 0 auto;
	width: 1300px;
}

.App-logo {
	height: 40vmin;
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #09d3ac;
}

.App-eventWrapper {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	margin-top: 50px;
}

.App-events {
	width: 800px;
	overflow-wrap: break-word;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: flex-start;
}

.App-event {
	text-align: left;
}
